<template lang="html">
  <div>
    <div class="header">
      <div class="cm-header">
        <span class="cm-header-icon" @click="$router.back(-1)">
          <i class="icon-back"></i>
        </span>
        <h1 class="cm-page-title"></h1>
      </div>
    </div>
    <div class="weui_cells weui_cells_access">
      <div class="weui_cell">
        <div class="weui_cell_bd weui_cell_primary">
          <p>:</p>
        </div>
        <div class="weui_cell_ft">
          <input type="text" placeholder="" v-model="name">
        </div>
      </div>
      <div class="weui_cell no_access" @click="showGender = true">
        <div class="weui_cell_bd weui_cell_primary">
          <p>:</p>
        </div>
        <div class="weui_cell_ft after">
          {{chooseGender}}
        </div>
      </div>
      <div class="weui_cell">
        <div class="weui_cell_bd weui_cell_primary">
          <p>:</p>
        </div>
        <div class="weui_cell_ft">
          <input type="text" placeholder="" v-model="card">
        </div>
      </div>
      <div class="weui_cell" @click="showType = true">
        <div class="weui_cell_bd weui_cell_primary">
          <p>:</p>
        </div>
        <div class="weui_cell_ft after">
          {{chooseType}}
        </div>
      </div>
    </div>
    <div class="weui_cells weui_cells_access" v-if="chooseType == ''">
      <div class="weui_cell">
        <div class="weui_cell_bd weui_cell_primary">
          <p> :</p>
        </div>
        <div class="weui_cell_ft">
          <input type="text" v-model="school">
        </div>
      </div>
      <div class="weui_cell">
        <div class="weui_cell_bd weui_cell_primary">
          <p> :</p>
        </div>
        <div class="weui_cell_ft">
          <input type="text" v-model="number">
        </div>
      </div>
      <div class="weui_cell no_access" @click="showYearSystem = true">
        <div class="weui_cell_bd weui_cell_primary">
          <p>:</p>
        </div>
        <div class="weui_cell_ft after">
          {{chooseYearSystem}}
        </div>
      </div>
      <div class="weui_cell" @click="showStartYear = true">
        <div class="weui_cell_bd weui_cell_primary">
          <p>:</p>
        </div>
        <div class="weui_cell_ft after">
          {{chooseStartYear}}
        </div>
      </div>
      <div class="weui_cell">
        <div class="weui_cell_bd weui_cell_primary">
          <p>:</p>
        </div>
        <div class="weui_cell_ft">
          <input type="text" v-model="from">
        </div>
      </div>
      <div class="weui_cell">
        <div class="weui_cell_bd weui_cell_primary">
          <p>:</p>
        </div>
        <div class="weui_cell_ft">
          <input type="text" v-model="to">
        </div>
      </div>
    </div>
    <transition name="pop-up">
      <div class="pop-up-box" v-if="showStartYear">
        <div class="choose">
          <p class="description">
            
          </p>
          <div class="gender">
            <p>
              <input type="radio" id="threen" value="2016" v-model="chooseStartYear">
              <label for="threen" :class="{selected:chooseStartYear == '2016'}"  @click="showStartYear = false;showMask = false">2016</label>
            </p>
            <p>
              <input type="radio" id="four" value="2015" v-model="chooseStartYear">
              <label for="four" :class="{selected:chooseStartYear == '2015'}"  @click="showStartYear = false;showMask = false">2015</label>
            </p>
             <p>
              <input type="radio" id="five" value="2014" v-model="chooseStartYear">
              <label for="five" :class="{selected:chooseStartYear == '2014'}"  @click="showStartYear = false;showMask = false">2014</label>
            </p>
          </div>
        </div>
      </div>
    </transition>
    <transition name="pop-up">
      <div class="pop-up-box" v-if="showYearSystem">
        <div class="choose">
          <p class="description">
            
          </p>
          <div class="gender">
            <p>
              <input type="radio" id="threen" value="3" v-model="chooseYearSystem">
              <label for="threen" :class="{selected:chooseYearSystem == '3'}"  @click="showYearSystem = false;showMask = false">3</label>
            </p>
            <p>
              <input type="radio" id="four" value="4" v-model="chooseYearSystem">
              <label for="four" :class="{selected:chooseYearSystem == '4'}"  @click="showYearSystem = false;showMask = false">4</label>
            </p>
             <p>
              <input type="radio" id="five" value="5" v-model="chooseYearSystem">
              <label for="five" :class="{selected:chooseYearSystem == '5'}"  @click="showYearSystem = false;showMask = false">5</label>
            </p>
          </div>
        </div>
      </div>
    </transition>
    <transition name="pop-up">
      <div class="pop-up-box" v-if="showGender">
        <div class="choose">
          <p class="description">
            
          </p>
          <div class="gender">
            <p>
              <input type="radio" id="man" value="" v-model="chooseGender">
              <label for="man" :class="{selected:chooseGender == ''}"  @click="showGender = false;showMask = false"></label>
            </p>
            <p>
              <input type="radio" id="woman" value="" v-model="chooseGender">
              <label for="woman" :class="{selected:chooseGender == ''}"  @click="showGender = false;showMask = false"></label>
            </p>
          </div>
        </div>
      </div>
    </transition>
    <transition name="pop-up">
      <div class="pop-up-box" v-if="showType">
        <div class="choose">
          <p class="description">
            
          </p>
          <div class="gender">
            <p>
              <input type="radio" id="adult" ref="adultNc" value="" v-model="chooseType">
              <label for="adult" :class="{selected:chooseType == ''}"  @click="showType = false;showMask = false"></label>
            </p>
            <p>
              <input type="radio" id="student" value="" v-model="chooseType">
              <label for="student" :class="{selected:chooseType == ''}"  @click="showType = false;showMask = false"></label>
            </p>
            <p>
              <input type="radio" id="child" value="" v-model="chooseType">
              <label for="child" :class="{selected:chooseType == ''}"  @click="showType = false;showMask = false"></label>
            </p>
          </div>
        </div>
      </div>
    </transition>
    <transition name="mask">
      <div class="mask" v-if="showGender || showType || showYearSystem || showStartYear"  @click="showGender = false,showType = false,showYearSystem = false,showStartYear = false"></div>
    </transition>
    <div class="botton" @click="sure"></div>
    <div class="remind" v-show="inputRemind"></div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      name: '',
      card: '',
      school: '',
      number: '',
      from: '',
      to: '',
      inputRemind: false,
      showGender: false,
      showType: false,
      showYearSystem: false,
      showStartYear: false,
      showMask: false,
      chooseGender: '',
      chooseType: '',
      chooseYearSystem: '4',
      chooseStartYear: '2015'
    }
  },
  methods: {
    sure () {
      if(this.name == '') {
        this.inputRemind = true
        setTimeout(() => this.inputRemind = false,1000)
        document.querySelector('.remind').innerHTML = '' 
      } 
      else if(this.card == '') {
        this.inputRemind = true
        setTimeout(() => this.inputRemind = false,1000)
        document.querySelector('.remind').innerHTML = ''
      } 
      else if(!/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(this.card)) {
        this.inputRemind = true
        setTimeout(() => this.inputRemind = false,1000)
        document.querySelector('.remind').innerHTML = ''
      }
      else if(this.chooseType == ''){
        if (this.school == '') {
          this.inputRemind = true
          setTimeout(() => this.inputRemind = false,1000)
          document.querySelector('.remind').innerHTML = ''
        }
        else if (this.number == '') {
          this.inputRemind = true
          setTimeout(() => this.inputRemind = false,1000)
          document.querySelector('.remind').innerHTML = ''
        }
        else if (this.from == '' || this.to == '') {
          this.inputRemind = true
          setTimeout(() => this.inputRemind = false,1000)
          document.querySelector('.remind').innerHTML = ''
        }
        else {
          this.$store.state.passenger = {}
          this.$store.state.passenger.name = this.name
          this.$store.state.passenger.gender = this.chooseGender
          this.$store.state.passenger.card = this.card
          this.$store.state.passenger.type = this.chooseType
          this.$store.state.passengerList.push(this.$store.state.passenger)
          this.$router.back(-1)
        } 
      }
      else {
        this.$store.state.passenger = {}
        this.$store.state.passenger.name = this.name
        this.$store.state.passenger.gender = this.chooseGender
        this.$store.state.passenger.card = this.card
        this.$store.state.passenger.type = this.chooseType
        this.$store.state.passengerList.push(this.$store.state.passenger)
        this.$router.back(-1)
      } 
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '../common/stylus/passenger.styl'
</style>



